@import "../../scss/variables";
@import "../../scss/mixins";

.revenueForecastBuTable {
  font-size: $tableFont;
  & > div {
    box-shadow: 0 0 0;
  }
  table {
    border-collapse: separate;
    // rowspan mixin for Material React Table
    @include mixer(50);
    tr {
      border-left: 1px solid $lightGray;
      &:nth-of-type(1) {
        th {
          &:nth-of-type(1) {
            z-index: 3;
          }
          &:nth-of-type(2) {
            z-index: 3;
          }
        }
      }

      &:nth-of-type(2) {
        th {
          &:nth-of-type(1) {
            z-index: 1;
          }

          &:has(.mixer) {
            border-top: 0;
          }
        }
      }

      th,
      td {
        &:nth-of-type(1) {
          position: sticky;
          // z-index: 20;
          left: 0;
          min-width: 60px;
          max-width: 60px;
        }

        &:nth-of-type(2) {
          min-width: 200px;
          position: sticky !important;
          // z-index: 20;
          left: 60px;
        }
      }

      th {
        vertical-align: middle;
      }
    }
    thead {
      tr {
        &:first-of-type {
          th {
            height: 25px;
          }
        }
        th {
          & > div {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
            text-align: center;
            line-height: 1.2;
          }
        }

        th:first-of-type {
          border-left: 1px solid #ccc;
        }

        th:nth-of-type(2) {
          & > div {
            display: flex;
            justify-content: center;
          }
        }
      }
    }
    tbody {
      tr {
        &:has(.hExpandedTxt) {
          background-color: $white;
        }

        td {
          &:nth-of-type(1) {
            z-index: 3;
          }

          &:nth-of-type(2) {
            z-index: 3;
          }
          // min-width: 100px;
          // max-width: 100px;
          div {
            // display: flex;
            align-items: center;
            gap: 5px;
            // width: 100%;

            img + span {
              margin-top: 3px;
              font-size: 12px;
            }
          }
          & > span {
            width: 100%;
            &:nth-of-type(2) {
              text-align: left;
            }
          }
        }

        td:first-of-type {
          border-left: 1px solid #ccc;
          max-width: 60px;

          button {
            height: 22px;
            width: 22px;
            padding-left: 27px;

            svg {
              height: 22px;
            }
          }
        }
      }
    }
  }
}
